import React, { Fragment, useState, useRef, useEffect, useCallback, useReducer } from 'react';
import PropTypes from 'prop-types';
import queryString from 'qs';
import { disableBodyScroll, enableBodyScroll, clearAllBodyScrollLocks } from 'body-scroll-lock';
import ModalContext from './context';
import defaultClassPrefix from '../defaultClassPrefix';
import generateCSS from './css';

const getSearchQuery = () => queryString.parse(
  window.location.search,
  { ignoreQueryPrefix: true },
);

const reduceModals = (state, action) => {
  const { type, slugs } = action;
  if (Array.isArray(slugs)) {
    if (type === 'add') return state.concat(slugs);
    if (type === 'remove') return state.some((slug) => !slugs.includes(slug));
  }
  if (type === 'clear') return [];
  return state;
};

const ModalProvider = (props) => {
  const {
    classPrefix: userClassPrefix,
    minifyCSS,
    generateCSS: shouldGenerateCSS,
    zIndex,
    handleParamChange,
    children,
    transTime,
  } = props;

  const containerRef = useRef(null);
  const [registeredModals, dispatchRegisteredModals] = useReducer(reduceModals, []);
  const [openModals, dispatchOpenModals] = useReducer(reduceModals, []);
  const [oneIsOpen, setOneIsOpen] = useState(false);
  const [willCloseOnBlur, setWillCloseOnBlur] = useState(false);
  const [bodyScrollIsLocked, setBodyScrollIsLocked] = useState(false);
  const [cssString, setCSSString] = useState('');
  const [classPrefix, setClassPrefix] = useState('');

  const closeAll = useCallback((updateHistory = true) => {
    if (updateHistory) {
      if (typeof handleParamChange === 'function') {
        handleParamChange({ type: 'clear' });
      }

      if (typeof handleParamChange === 'boolean' && handleParamChange) {
        const searchQuery = { ...getSearchQuery() };
        delete searchQuery.modal;

        const queryWithoutModal = queryString.stringify(searchQuery,
          {
            addQueryPrefix: true,
            encode: false,
          });

        const newURL = `${window.location.pathname}${queryWithoutModal}`;
        window.history.pushState({}, '', newURL);
      }
    }

    clearAllBodyScrollLocks();

    dispatchOpenModals({ type: 'clear' });
  }, [handleParamChange]);

  const bindEsc = useCallback((e) => {
    if (e.keyCode === 27) closeAll();
  }, [closeAll]);

  useEffect(() => {
    const { modal } = getSearchQuery();
    dispatchOpenModals({ type: 'addBulk', slug: modal });
  }, []);

  useEffect(() => {
    document.addEventListener('keydown', (e) => bindEsc(e), false);
    return () => document.removeEventListener('keydown', (e) => bindEsc(e), false);
  }, [bindEsc]);

  useEffect(() => {
    let newClassPrefix = defaultClassPrefix;
    if (typeof userClassPrefix === 'string' && userClassPrefix) newClassPrefix = userClassPrefix;
    if (typeof userClassPrefix === 'boolean' && !userClassPrefix) newClassPrefix = undefined;
    setClassPrefix(newClassPrefix);
  }, [userClassPrefix]);

  useEffect(() => {
    if (shouldGenerateCSS) {
      let newString = '';
      newString = generateCSS(classPrefix, zIndex);
      if (minifyCSS) newString = newString.replace(/\n/g, '').replace(/\s\s+/g, ' ');
      setCSSString(newString);
    }
  }, [shouldGenerateCSS, minifyCSS, zIndex, classPrefix]);

  const open = useCallback((slug, skipParamChange) => {
    if (!skipParamChange) {
      if (typeof handleParamChange === 'function') {
        handleParamChange({ type: 'add', slug });
      }

      if (typeof handleParamChange === 'boolean' && handleParamChange) {
        const searchQuery = getSearchQuery();
        const { modal } = searchQuery;
        const newSearchQuery = { ...searchQuery };

        if (modal) {
          if (typeof modal === 'string') newSearchQuery.modal = [modal, slug];
          if (Array.isArray(modal)) newSearchQuery.modal.push(slug);
        } else newSearchQuery.modal = slug;

        const queryWithModal = queryString.stringify(
          newSearchQuery,
          {
            addQueryPrefix: true,
            encode: false,
          },
        );

        const newURL = `${window.location.pathname}${queryWithModal}`;
        window.history.pushState({}, '', newURL);
      }
    }

    dispatchOpenModals({ type: 'add', slug });
  }, [handleParamChange]);

  const close = useCallback((slug, skipParamChange) => {
    if (!skipParamChange) {
      if (typeof handleParamChange === 'function') {
        handleParamChange({ type: 'remove', slug });
      }

      if (typeof handleParamChange === 'boolean' && handleParamChange) {
        const searchQuery = { ...getSearchQuery() };
        const { modal } = searchQuery;

        if (modal) {
          if (typeof modal === 'string' && modal === slug) delete searchQuery.modal;
          if (Array.isArray(modal)) searchQuery.modal = modal.filter((item) => item !== slug);
        }

        const queryWithoutModal = queryString.stringify(
          searchQuery,
          {
            addQueryPrefix: true,
            encode: false,
          },
        );

        const newURL = `${window.location.pathname}${queryWithoutModal}`;
        window.history.pushState({}, '', newURL);
      }
    }

    dispatchOpenModals({ type: 'remove', slug });
  }, [handleParamChange]);

  const toggle = useCallback((slug) => {
    if (openModals.includes(slug)) close(slug);
    else open(slug);
  }, [close, open, openModals]);

  const setBodyScrollLock = useCallback((shouldLock, excludingRef) => {
    if (shouldLock) {
      disableBodyScroll(excludingRef.current);
      setBodyScrollIsLocked(true);
    } else {
      enableBodyScroll(excludingRef.current);
      setBodyScrollIsLocked(false);
    }
  }, []);

  const setContainerRef = useCallback((ref) => {
    containerRef.current = ref;
  }, []);

  useEffect(() => {
    setOneIsOpen(openModals.some((modal) => registeredModals.includes(modal)));
  }, [openModals, registeredModals]);

  const inheritedProps = { ...props };
  delete inheritedProps.children;

  return (
    <Fragment>
      {shouldGenerateCSS && <style dangerouslySetInnerHTML={{ __html: cssString }} />}
      <ModalContext.Provider value={{
        // props
        ...inheritedProps,
        transTime,
        // state
        containerRef,
        registeredModals,
        openModals,
        oneIsOpen,
        willCloseOnBlur,
        bodyScrollIsLocked,
        classPrefix,
        // methods
        toggle,
        open,
        close,
        closeAll,
        register: (slug) => {
          console.log('register', slug);
          dispatchRegisteredModals({ type: 'add', slug });
        },
        unregister: (slug) => {
          // console.log('unregister', slug);
          dispatchRegisteredModals({ type: 'remove', slug });
        },
        setContainerRef,
        setBodyScrollLock,
        setWillCloseOnBlur,
      }}
      >
        {children && children}
      </ModalContext.Provider>
    </Fragment>
  );
};

ModalProvider.defaultProps = {
  generateCSS: true,
  minifyCSS: true,
  classPrefix: undefined,
  handleParamChange: undefined,
  transTime: 1000,
  zIndex: 9999,
  children: undefined,
};

ModalProvider.propTypes = {
  generateCSS: PropTypes.bool,
  minifyCSS: PropTypes.bool,
  classPrefix: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.bool,
  ]),
  handleParamChange: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.bool,
  ]),
  transTime: PropTypes.number,
  zIndex: PropTypes.number,
  children: PropTypes.node,
};

export default ModalProvider;
